import * as React from 'react'
import * as pc from "playcanvas";
import subaru from "./subaru/Subaru Impreza Free.glb";
import camoTex from "./subaru/textures/camo.jpg";
import camoTex2 from "./subaru/textures/camo2.png";
import ogTex from "./subaru/53013832/ogLivery.jpg";
import redTex from "./subaru/textures/red.jpg";

var newTex;
class App extends React.Component {
    constructor(props) {
        super(props)
        this.canvas = undefined
        this.renderer = null;
        this.car = null;
        this.app = null;
        this.reactCanvas = React.createRef();

    }

    componentDidMount() {
        this.canvas = this.reactCanvas.current;
        this.canvas.width = this.canvas.offsetWidth;
        this.canvas.height = this.canvas.offsetHeight;
        this.renderModel();
    }

    componentWillUnmount() {
        window.removeEventListener('resize', () => {
            this.renderer.resizeCanvas();
        });
    }

    renderModel = (theTex) => {
        var app = new pc.Application(this.canvas, {});
        app.start();

        // fill the available space at full resolution
        app.setCanvasFillMode(pc.FILLMODE_FILL_WINDOW);
        app.setCanvasResolution(pc.RESOLUTION_AUTO);

        // ensure canvas is resized when window changes size
        window.addEventListener('resize', function () {
            app.resizeCanvas();
        });


        // create camera entity
        var camera = new pc.Entity('camera');
        camera.addComponent('camera', {
            clearColor: new pc.Color(0.1, 0.1, 0.1)
        });

        // create directional light entity
        var light = new pc.Entity('light');
        light.addComponent('light');

        // add to hierarchy

        app.root.addChild(camera);
        app.root.addChild(light);

        // set up initial positions and orientations
        camera.setPosition(0, 0, 3);
        light.setEulerAngles(45, 0, 0);

        //load textures
        app.assets.loadFromUrl(camoTex, "texture", function (err, asset) {

        });
        app.assets.loadFromUrl(camoTex2, "texture", function (err, asset) {

        });
        app.assets.loadFromUrl(ogTex, "texture", function (err, asset) {

        });
        app.assets.loadFromUrl(redTex, "texture", function (err, asset) {

        });
        //load car model
        var entity = new pc.Entity();
        app.assets.loadFromUrl(subaru, "model", function (err, asset) {
            entity.addComponent("model");
            entity.model.model = asset.resource;
            entity.name = "car";
            entity.setLocalPosition(0, 0, -5);

            if (theTex !== undefined) {
                console.log(theTex);
                asset.resource.meshInstances[8]._material._diffuseMap = theTex;
            }
            pc.app.root.addChild(entity);
        });


        app.on('update', function (deltaTime) {
            entity.rotate(0, 10 * deltaTime, 0);
        });
        this.app = app;
        this.renderer = app;
        this.car = entity;
    }
    printCar(tex) {
        console.log(pc.app.root);
        this.car.destroy();
        console.log(this.car);

        this.app.assets.loadFromUrl(tex, "texture", function (err, asset) {
            newTex = asset.resource;

        });
        this.renderModel(newTex);
    }
    clickHandler = event => {
        const {
            id
        } = event.target
        const handlers = {
            textureButton1: () => {

                this.printCar(ogTex);

            },
            textureButton2: () => {

                this.printCar(redTex);

            },
            
            textureButton3: () => {

                this.printCar(camoTex);

            },
            textureButton4: () => {

                this.printCar(camoTex2);

            },
        }

        handlers[id]();
    }

    render(){
        return (
          <div>
            <canvas ref={this.reactCanvas} id='3d-drawing-canvas'>This browser doesn't support canvas</canvas>
            <button onClick={this.clickHandler} id='textureButton1' style={{position:'fixed', top: 0, left: 0, width:'200px', height:'50px'}}>
          Original
          </button>
          <button onClick={this.clickHandler} id='textureButton2' style={{position:'fixed', top: 50, left: 0, width:'200px', height:'50px'}}>
          Red
          </button>
          <button onClick={this.clickHandler} id='textureButton3' style={{position:'fixed', top: 100, left: 0, width:'200px', height:'50px'}}>
          Camo1
          </button>
          <button onClick={this.clickHandler} id='textureButton4' style={{position:'fixed', top: 150, left: 0, width:'200px', height:'50px'}}>
          Camo2
          </button>
          
          <input type="color" id="colorpicker" value="#0000ff"></input>
         </div>
        )
    }
}
export default App;